import React, { useMemo } from "react"
import { useUser } from "@clerk/clerk-react"
import { Box } from "@mui/material"
import { getUserInitials } from "@rp/common/utils"
import { useDeviceInfo } from "@rp/react/web/common/hooks"

const UserInitialsBox = () => {
  const { isMobile } = useDeviceInfo()
  const { user } = useUser()
  const userInitials = useMemo(() => getUserInitials(user?.fullName), [user])

  return (
    <Box
      sx={{
        height: isMobile ? 28 : 40,
        width: isMobile ? 28 : 40,
        color: "rgba(255, 255, 255, 0.6)",
        backgroundColor: "overlay.surface",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        borderRadius: 1,
        fontSize: isMobile ? "body2.fontSize" : "body1.fontSize",
        flexShrink: 0,
      }}
    >
      {userInitials}
    </Box>
  )
}

export { UserInitialsBox }
